<template>
    <div>
        <div class="left-nav">
            <h2>模版的页签</h2>
            <Divider />
            <Card v-for="(item,index) in cardList" :key="index" :style="{marginTop: '16px',cursor:'pointer',position: 'relative',width:'180px',height:'250px',backgroundColor:activeCardIndex === index ? '#e8eaec' : ''}" >
                <div style="text-align:center"  @click="changeActiveCard(index)">
                    <img :src="item.bgImage" class="temp_sh_bg" />
                    <span>{{index+1}} / {{cardList.length}}</span>
                </div><Icon type="md-close" v-if="cardList.length > 1 && !item.readOnly" :style="{position:'absolute',top:'4px',right:'4px',display:activeTempIndex === 0 || useTemplateIndex === 0 ? 'block':'none'}"  @click="cardRemove(index)"/>
            </Card>
            <Card :style="{textAlign:'center',cursor:'pointer',width:'180px',height:'120px',marginTop:'20px',display:activeTempIndex === 0 || useTemplateIndex === 0 ? 'block':'none'}">
                <div @click="addCard()">
                    <h1 style="text-align: center;">+</h1>
                    <h3 style="text-align: center;">添加一个新的卡片</h3>
                </div>
            </Card>
        </div>
        <div class="right-nav" v-if="useType == 'use'">
            <Tabs  v-model="currentTab">
                <TabPane label="属性" name="attr_panel">
                    <div>
                        <ul class="comp_ul">
                            <li class="comp_li" >
                                横轴距离: <input v-model="compEditAttr.left"/>
                            </li>
                            <li class="comp_li" >
                                纵轴距离: <input v-model="compEditAttr.top"/>
                            </li>
                            <li class="comp_li" v-if="activeCompType == 'font'">
                                字体颜色: <input  v-model="compEditAttr.color"/>
                            </li>
                            <li class="comp_li" v-if="activeCompType == 'font'">
                                文字内容: <textarea  v-model="compEditAttr.text" style="vertical-align:top;" wrap="hard"></textarea>
                            </li>
                            <li class="comp_li" v-if="activeCompType == 'font'">
                                字体大小: <input  v-model="compEditAttr['font-size']"/>
                            </li>
                            <li class="comp_li">
                                组件顺序: <input v-model="compEditAttr['z-index']"/>
                            </li>
                            <input type="hidden" id="imageSrc" v-model="compEditAttr.src"/>
                            <li class="comp_li" v-if="activeCompType == 'image'">
                                图片文件: <input type="file" id="UploadImageFile"/><button @click="clearImageFile">Cancel</button><button @click="uploadImage">OK</button>
                            </li>
                        </ul>
                    </div>
                </TabPane>
                <TabPane label="组件" style="color:white;" >
                    <div>
                        <ul class="comp_list">
                            <li class="comp_col" @click="addFont">
                                <div class="widget-icon-wrapper"><Icon class="icon-f" type="logo-tumblr" /></div>
                                <span>文字组件</span>
                            </li>
                            <li class="comp_col" @click="addImage">
                                <div class="widget-icon-wrapper"><Icon  class="icon-f" type="ios-image"/></div>
                                <span>普通图片组件</span>
                            </li>
                        </ul>
                    </div>
                </TabPane>
            </Tabs>
        </div>
        <div class="center-content">
            <div v-if="useType == 'use'">
                <Alert type="warning">
                    建议在Microsoft Edge 或者 Chrome(谷歌)浏览器 下操作,最终的显示结果以终端设备显示为准，本页面设计出来的效果会自动适应手机高度和宽度
                </Alert>
            </div>
            <div :style="{ textAlign:'center',marginTop:'20px'}" v-if="useType == 'use'">
                <Input placeholder="请输入发布后页签的标题..." style="width: 300px" v-model="publishTitle"/>
            </div>
            <div  class="phone" ref="pub_area">
                <div class="phone_view" name="phone_view" ref="phone_view">
                    <div v-for="(card,cardIndex) in cardList" :key="cardIndex" :style="{ backgroundImage: 'url('+card.bgImage +')',display:cardIndex === activeCardIndex ? 'block':'none',backgroundSize: 'inherit',width:'100%',height:'100%'}" >
                        <component v-for="(item,index) in card.compmonents" :key="index" :is="item.component" :width="item.customAttr.width" :height="item.customAttr.height"
                                   :top="item.customCssAttr.top" :left="item.customCssAttr.left" :color="item.customCssAttr.color" :index="item.customCssAttr['z-index']" :src="item.customAttr.src"
                                   @selectcomponent="() => selectComponent(item)" :text="item.customAttr.text"  :fontsize="item.customCssAttr['font-size']"/>
                    </div>
                </div>
            </div>
            <div :style="{ textAlign:'center',marginTop:'40px'}" v-if="useType == 'use'">
                <Button type="warning" @click="openPublishWindow" style="width:100px;" :loading="idPublishing">保 存</Button>
                <Upload
                    action="//jsonplaceholder.typicode.com/posts/" :style="{ marginLeft:'2px',marginTop:'20px'}" >
                    <Button icon="md-bookmarks" v-if="useTemplateIndex === 0" >更换新的背景图片</Button>
                </Upload>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import '../../assets/css/temp-common.css';
import { objEqual } from '@/libs/tools';
import md5 from 'js-md5';
import * as qiniu from 'qiniu-js';
export default {
    name: 'TemplateCenter',
    props: {
        useTemplateIndex: {
            type: Number,
            default: -1
        },
        useType: {
            type: String,
            default: 'view'
        },
        lastContentInfo: {
            type: Object
        }
    },
    data () {
        return {
            activeTempIndex: -1,
            activeCardIndex: -1,
            activeCompKey: '',
            activeCompType: '',
            cardList: [],
            compOldEditAttr: { },
            compEditAttr: { },
            currentTab: '',
            idPublishing: false,
            publishTitle: ''
        };
    },
    computed: {
        ...mapGetters([
            'getBasicTemplateList'
        ])
    },
    watch: {
        compEditAttr: {
            handler (newValue, oldValue) {
                const st = this;
                if (objEqual(newValue, this.compOldEditAttr)) return;
                let changeAttrName = '';
                let changeAttrValue = '';
                for (const i in newValue) {
                    if (this.compOldEditAttr[i] !== newValue[i]) {
                        changeAttrName = i;
                        changeAttrValue = newValue[i];
                    }
                }
                this.$store.commit('changeComponentAttr', {
                    tempIndex: st.activeTempIndex,
                    cardIndex: st.activeCardIndex,
                    compKey: st.activeCompKey,
                    targetAttr: changeAttrName,
                    targetAttrValue: changeAttrValue
                });
            },
            deep: true
        }
    },
    methods: {
        async uploadImage () {
            const files = document.getElementById('UploadImageFile');
            const file = files.files[0];
            if (file === undefined) {
                this.$Message.warning('文件为空!');
                return;
            }
            const result = await this.get('/api/nmedia/config/qiniu/info');
            if (result.success) {
                const webToken = result.value.token;
                const webHost = result.value.host;
                this.uploadMain(file, webToken, webHost);
            }
        },
        clearImageFile () {
            document.getElementById('UploadImageFile').value = '';
        },
        imageChange (src) {
            const st = this;
            this.$store.commit('changeComponentAttr', {
                tempIndex: st.activeTempIndex,
                cardIndex: st.activeCardIndex,
                compKey: st.activeCompKey,
                targetAttr: 'src',
                targetAttrValue: src
            });
            this.compEditAttr.src = src;
        },
        addFont () {
            const st = this;
            this.$store.commit('addFont', {
                tempIndex: st.activeTempIndex,
                cardIndex: st.activeCardIndex
            });
        },
        addImage () {
            const st = this;
            this.$store.commit('addImage', {
                tempIndex: st.activeTempIndex,
                cardIndex: st.activeCardIndex
            });
        },
        selectComponent (item) {
            // 获取该组件的可编辑属性
            const tempIndex = this.activeTempIndex;
            const cardIndex = this.activeCardIndex;
            const compKey = item.key;
            const targetAttr = this.$store.getters.getComponentEditAttr({ tempIndex, cardIndex, compKey });
            //  清空上个组件的属性值
            for (const key in this.compEditAttr) {
                this.$delete(this.compEditAttr, key);
            }
            // 必须用Vue内置的动态添加对象属性的方法，不然无法检测到属性值的变化
            for (const attr in targetAttr) {
                this.$set(this.compEditAttr, attr, targetAttr[attr]);
            }
            // 对象的复制，不是引用复制
            this.compOldEditAttr = JSON.parse(JSON.stringify(this.compEditAttr));
            this.activeCompKey = compKey;
            this.activeCompType = item.type;
            this.currentTab = 'attr_panel';
        },
        useDefined () {
            this.useTemplate(0);
        },
        buildHtml () {
            const targetTemp = this.$store.getters.getTargetTemplate(this.activeTempIndex);
            const refHtml = [];
            for (const card of targetTemp.cardList) {
                refHtml.push(`<div class="swiper-slide" style="background-image:url('${card.bgImage}');background-repeat: no-repeat;background-size: inherit;">`);
                const componentList = card.compmonents;
                for (const comp of componentList) {
                    let cssStr = '';
                    let attrStr = '';
                    for (const attr in comp.customCssAttr) {
                        cssStr += `${attr}:${comp.customCssAttr[attr]};`;
                    }

                    for (const attr in comp.customAttr) {
                        // 后面结束的空格为必须
                        attrStr += `${attr}="${comp.customAttr[attr]}" `;
                    }
                    const row = `<${comp.targetDom} ${attrStr} style="${cssStr}">${comp.customAttr.text || ''}</${comp.targetDom}>`;
                    const newHtml = comp.callHtml(row);
                    refHtml.push(newHtml);
                }
                refHtml.push('</div>');
            }
            return refHtml;
        },
        async submitPublish () {
            if (this.publishTitle.trim() === '') {
                this.$Message.warning('发布后的标题不能为空!');
                return;
            }
            this.idPublishing = true;
            const htmlAry = this.buildHtml();
            const result = await this.post(`/api/nmedia/qiniu/publish?tempTitle=${this.publishTitle}`, htmlAry);
            if (result.success) {
                this.$store.commit('changeLastSavePageAttr', { url: result.value.pubUrl, title: this.publishTitle.trim(), dataId: result.value.id });
                this.$emit('publishsuccess');
            } else {
                this.$Notice.error({
                    title: '内容保存失败',
                    desc: '并未产生公网的预览地址'
                });
            }
            this.idPublishing = false;
        },
        openPublishWindow () {
            this.submitPublish();
        },
        changeActiveCard (index) {
            this.activeCardIndex = index;
        },
        useTemplate (index) {
            this.cardList = this.$store.getters.getCardList(index);
            this.activeTempIndex = index;
            this.activeCardIndex = 0;
        },
        addCard () {
            const st = this;
            this.$store.commit('addEmptyCard', {
                tempIndex: st.activeTempIndex
            });
            this.changeActiveCard(this.cardList.length - 1);
        },
        cardRemove (index) {
            const st = this;
            this.$store.commit('removeCard', {
                tempIndex: st.activeTempIndex,
                targetCardIndex: index
            });
            if (index === this.activeCardIndex) {
                this.changeActiveCard(0);
            }
        },
        uploadMain (file, token, host) {
            // 文件资源名
            const key = `card/${md5(file.name)}`;
            const config = {
                useCdnDomain: true,
                region: qiniu.region.z2
            };
            const putExtra = {
                fname: file,
                params: {},
                mimeType: ['image/png', 'image/jpeg', 'image/gif']
            };
            const _this = this;
            const observe = {
                next (res) {
                },
                error (err) {
                    _this.$Notice.success({
                        title: '临时图片上传失败，请稍候再试',
                        desc: err.message
                    });
                },
                complete (res) {
                    // 上传成功以后会返回key 和 hash  key就是文件名
                    const uploadImageSrc = `${host}/${res.key}`;
                    _this.imageChange(uploadImageSrc);
                }
            };
            // 开始上传
            const observable = qiniu.upload(file, key, token, putExtra, config);
            observable.subscribe(observe);
        }

    }
};
</script>
